@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@500;700&family=Jost&family=PT+Sans&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  background-color: #181818;
  scroll-behavior: smooth;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Scrollbar*/

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #202020;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #555;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

.logo-tmdb {
    cursor: pointer;
}

.trending {
    display: flex;
    flex-wrap: wrap;
    float: left;
    justify-content: center;
    margin: 10px 10px;
    padding: 5px;
}

.trending article {
    background: transparent;
    color: whitesmoke;
    font-weight: bold;
    margin-bottom: 20px;
    margin: 5px;
    text-align: center;
    width: 185px;
}

.trending article:hover {
    color: #eb4034;
    transition: all 0.5s;
}

.trending article img {
    border-radius: 10px;
    transition: all 0.5s;
}

.trending article:hover img {
    -webkit-filter: sepia(50%) brightness(80%) contrast(120%);
            filter: sepia(50%) brightness(80%) contrast(120%);
}

.searchbar {
    -webkit-transition: width 0.4s ease-in-out;
    background: transparent;
    border: none;
    border-bottom: 1px solid #0694bb;
    float: right;
    font-size: 20px;
    margin-right: 15px;
    margin-top: calc((70px - 35px)/2);
    transition: all 0.2s ease-in-out;
    width: 130px;
}

.searchbar:focus {
    border-bottom: 2px solid #0694BB;
    outline: none;
    width: 200px;
}

.btn-search{
    background-color: #01b3e4;
    border-radius: 8px;
    border: thin solid transparent;
    color: gray;
    float: right;
    margin-right: 10px;
    margin-top: calc((70px - 24px)/2);
    transition: all 0.2s ease-in-out;
}

.btn-search i{
    margin: auto;
}

.btn-search:hover{
    background-color: transparent;
    border: thin solid #01b3e4;
    cursor: pointer;
}
.btn-search:focus{
    outline: none;
}
*{
    color: whitesmoke;
}

.movie{
    display: flex;
    margin: 40px
}
.movie .poster{
    max-height: 750px;
    border-radius: 10px;
}

.movie-info img{
    border-radius: 10px;
}
.movie-info{
    margin: 50px;
    margin-left: 50px;
}

.movie-info h1{
    font-family: 'Comfortaa', cursive;
}
.movie-info h3, p{
    font-family: 'Jost', sans-serif;
}

.checked{
    color: orange;
}
.cast{
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.cast .actor{
    border-radius: 10px;
    margin: 10px;
}
nav {
    background-color: #202020;
    height: 70px;
    width: 100%;
}

.logo-tmdb {
    margin-top: 15px;
    margin-left: 15px;
    width: 100px;
}

i {
    margin-top: 22px;
    margin-right: 20px;
    float: right;
    transition: 0.2s all;
}

i:hover {
